footer {
  background: var(--color-primary);
  padding: 2rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a {
  color: var(--color-bg);
}

.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer_socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
}

.footer_socials a {
  background-color: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}

.footer_socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer_copyright {
  margin-top: 6rem;
  margin-bottom: -1rem;
  color: var(--color-bg);
}

/*==================== Media Queries (Medium) =====================*/
@media screen and (max-width: 1024px) {
  .footer_logo {
    margin-bottom: 1.4rem;
  }

  .permalinks {
    gap: 1.4rem;
    margin-bottom: 1.2rem;
  }

  .footer_socials {
    margin-bottom: 2rem;
  }
}

/*==================== Media Queries (Small) =====================*/
@media screen and (max-width: 600px) {
  .permalinks {
    gap: 1rem;
    margin-bottom: 1rem;
  }

  .footer_socials {
    margin-bottom: 1.2rem;
  }
}
