header {
  height: 100vh;
  padding-top: 6rem;
  overflow: hidden;
}

.header_container {
  text-align: center;
  height: 100%;
  position: relative;
  cursor: default;
}

.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials a {
  font-size: 1.2rem;
}

.header_socials::after {
  content: "";
  width: 2px;
  height: 2rem;
  background: var(--color-primary);
}

.me {
  background: linear-gradient(var(--color-primary), transparent);
  aspect-ratio: 1/1;
  width: auto;
  height: auto;
  max-width: 20rem;
  position: absolute;
  left: calc(50% - 10rem);
  margin-top: 5rem;
  border-radius: 50%;
  overflow: hidden;
  padding: 2rem 1rem 1rem 1rem;
}

.scroll_down {
  position: absolute;
  right: -2rem;
  bottom: 7rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 1.1rem;
  letter-spacing: 2px;
}

/*==================== Media Queries (Medium) =====================*/
@media screen and (max-width: 1024px) {
  header {
    height: 75vh;
  }
}

/*==================== Media Queries (Small) =====================*/
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .header_socials,
  .scroll_down {
    display: none;
  }
}
