.about_container {
  display: grid;
  grid-template-columns: 25% 70%;
  gap: 5%;
}

.about_me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about_image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about_image:hover {
  transform: rotate(0);
}

.about_cards {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 0.8rem;
}

.about_card {
  background: var(--color-bg-variant);
  border: 2px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about_card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about_icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1.4rem;
}

.about_card small {
  font-size: 0.7rem;
  color: var(--color-light);
  display: flex;
  margin-bottom: 0.1rem;
  justify-content: center;
}

.about_content p {
  color: var(--color-light);
  margin: 2rem 0 2.6rem;
}

.about_content p h3 {
  display: inline;
}

/*==================== Media Queries (Medium) =====================*/
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about_cards {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1rem;
  }

  .about_content p {
    margin: 1rem auto 1.5rem;
  }

  .about_content {
    text-align: center;
  }
}

/*==================== Media Queries (Small) =====================*/
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about_cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .about_content p {
    margin: 1.5rem 0;
  }
}
